.top-slider {
    margin-left: 74px;
    max-width: 523px;
    width: 100%;
    .slick-prev,
    .slick-next {
        width: 48px;
        height: 48px;
        background: #E89F71;
        border-radius: 50%;
        -webkit-border-radius: 50%;
        -moz-border-radius: 50%;
        -ms-border-radius: 50%;
        -o-border-radius: 50%;
        z-index: 2;
        @include transition;
    }
    .slick-prev:before {
        content: '';
        display: block;
        position: absolute;
        top: 0;
        right: 0;
        left: 0;
        bottom: 0;
        background: url(../img/prev-sl.svg) no-repeat center;
        @include transition;
    }
    .slick-next:before {
        content: '';
        display: block;
        position: absolute;
        top: 0;
        right: 0;
        left: 0;
        bottom: 0;
        background: url(../img/next-sl.svg) no-repeat center;
        @include transition;
    }
    .slick-disabled {
        opacity: 0.3;
    }
    .slick-prev.slick-disabled:before,
    .slick-next.slick-disabled:before {
        opacity: 1;
    }
    .slick-dots {
        bottom: 15px;
    }
    .slick-dots li {
        position: relative;
        width: 9px;
        height: 9px;
        margin: 0 11.5px;
        @include transition;
    }
    .slick-dots li button {
        position: absolute;
        top: 0;
        right: 0;
        left: 0;
        bottom: 0;
        width: 9px;
        height: 9px;
        background: #fff;
        padding: 0;
        border-radius: 50%;
        -webkit-border-radius: 50%;
        -moz-border-radius: 50%;
        -ms-border-radius: 50%;
        -o-border-radius: 50%;
        @include transition;
    }
    .slick-dots li button:before {
        content: '';
        display: block;
        position: absolute;
        top: 50%;
        left: 50%;
        width: 22.5px;
        height: 22.5px;
        border-radius: 50%;
        -webkit-border-radius: 50%;
        -moz-border-radius: 50%;
        -ms-border-radius: 50%;
        -o-border-radius: 50%;
        border: 1px solid #E89F71;
        box-sizing: border-box;
        background: transparent;
        opacity: .7;
        opacity: 0;
        @include transition;
        transform: translate(-50%, -50%);
        -webkit-transform: translate(-50%, -50%);
        -moz-transform: translate(-50%, -50%);
        -ms-transform: translate(-50%, -50%);
        -o-transform: translate(-50%, -50%);
    }
    li.slick-active button {
        background: #E89F71;
    }
    li.slick-active button::before {
        opacity: 1;
    }
}

