        font-size: 40px;
        line-height: 120%;
        text-align: center;
        color: #3A3A3A;
        margin-bottom: 30px;
    }
}

.rewiews-slider {
    margin: 0 -6px;
    margin-bottom: 40px;
    .slick-list {
        padding: 0 6px;
    }
}

.rewiews-slider__item {
    max-width: 384px;
    width: 100%;
    margin: 0 6px;
    outline: none;
}

.rew-content {
    padding: 16px 15px 10px 15px;
    background: #FFFFFF;
    border: 1px solid #D4D4D3;
    border-top: none;
    box-sizing: border-box;
    border-radius: 2px;
    p {
        font-weight: normal;
        font-size: 14px;
        line-height: 24px;
        color: #18191F;
    }
}

.rew-top {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 10px;
}

.ew-name,
.exs-date {
    font-weight: normal;
    font-size: 24px;
    line-height: 32px;
    color: #18191F;
}

.rew-img {
    position: relative;
    height: 0;
    padding-bottom: 53.125%;
    img {
        position: absolute;
        top: 0;
        right: 0;
        left: 0;
        bottom: 0;
        width: 100%;
        height: 100%;
        object-fit: cover;
        object-position: center;
    }
}

.rewiews-slider {
    .slick-prev,
    .slick-next {
        width: 48px;
        height: 48px;
        background: #FFFFFF;
        box-shadow: 0px 4px 14px 1px rgba(0, 0, 0, 0.16);
        border-radius: 50%;
        -webkit-border-radius: 50%;
        -moz-border-radius: 50%;
        -ms-border-radius: 50%;
        -o-border-radius: 50%;
        z-index: 2;
    }
    .slick-prev:before,
    .slick-next:before {
        content: '';
        display: block;
        position: absolute;
        top: 0;
        right: 0;
        left: 0;
        bottom: 0;
        margin: auto;
        @include transition;
    }
    .slick-prev {
        left: -60px;
    }
    .slick-next {
        right: -60px;
    }
    .slick-prev:before {
        background: url(../img/prev-orange.svg) no-repeat center;
    }
    .slick-next:before {
        background: url(../img/next-orange.svg) no-repeat center;
    }
}

.footer-box {
    display: flex;
    justify-content: flex-start;
    align-items: flex-start;
    background: rgba(0, 0, 0, 0.07);
    backdrop-filter: blur(5px);
    padding: 36px 30px 30px 30px;
    margin-bottom: 2px;
    .logo {
        margin-right: 85px;
    }
    .logo span {
        font-size: 57px;
        margin-bottom: 22px;
    }
}

.bottom-img {
    position: absolute;
    bottom: 0;
    left: 0;
    right: 0;
    width: 100%;
    z-index: -1;
}

footer {
    .container {
        max-width: 1415px;
    }
    .logo {
        span,
        i {
            color: #fff;
        }
    }
}

.copyright {
