        color: #FFFFFF;
    }
}

.footer-info {
    display: flex;
    width: 72%;
    justify-content: space-between;
    align-items: flex-start;
    b {
        display: block;
        font-weight: 500;
        font-size: 18px;
        line-height: 22px;
        color: #FFFFFF;
        margin-bottom: 17px;
    }
    .address {
        color: #fff;
        margin-bottom: 20px;
    }
    .mail {
        font-weight: 500;
        font-size: 14px;
