        margin-bottom: 22px;
    }
    .time+span {
        font-weight: 500;
        font-size: 14px;
        line-height: 17px;
        color: #FFFFFF;
    }
    .tel {
        display: block;
        padding: 0 0 0 23px;
        background: url(../img/tel-ico.svg) no-repeat left center;
        font-weight: 500;
        font-size: 16px;
        line-height: 19px;
        color: #FFFFFF;
        &:first-child {
